.search {
    position: relative;
    top: 0;
    right: 0;
    margin: 0 1.5rem;

    &__input {
       width: 100%;
       padding: 12px;
       border: 1px solid #000;
       border-radius: 10px;
       outline: none;
       border-right: none;
    }

    &__submit {
        position: absolute;
        right: 0;
        width: 20%;
        font-size: 1.6rem;
        font-weight: 700;
        outline: none;
        background-color: #007bff;
        color: #fff;
        border: none;
        height: 100%;
        border-bottom-right-radius: 8px;
        border-top-right-radius: 8px;
        cursor: pointer;

        @media screen and (min-width: 479px) {
            width: 15%;
        }
    
        @media screen and (min-width: 769px) {
            width: 10%;
        }
    
    }
}